import { StaffModel, StaffService } from '../service';

import { TCampaign, THit, TLIB, TSearchTerm } from './types';

//
// |
//

class LIBService extends StaffService<TLIB> {
  hitList(params?: URLSearchParams) {
    return this.get(`${this._path}hits/`, { params });
  }
}

export const LIB = (data: TLIB = {}) => {
  return StaffModel<TLIB, LIBService>(data, 'libs', LIBService);
};

export type TLIBInstance = ReturnType<typeof LIB>;

// /
//
// /

class HitService extends StaffService<THit> {
  deleteSlugs(slugs: string[]) {
    return this.post(`${this._endpoint}batch/`, { slugs, action: 'destroy' });
  }

  getSummary(params?: URLSearchParams) {
    return this.get(`${this._endpoint}summary/`, { params });
  }
}

export const Hit = (data: THit = {}) => {
  return StaffModel<THit, HitService>(data, 'hits', HitService);
};

export type THitInstance = ReturnType<typeof Hit>;

//
//
//

class CampaignService extends StaffService<TCampaign> {}

export const Campaign = (data: TCampaign = {}) => {
  return StaffModel<TCampaign, CampaignService>(data, 'campaigns', CampaignService);
};

export type TCampaignInstance = ReturnType<typeof Campaign>;

//
//
//

class SearchTermService extends StaffService<TSearchTerm> {}

export const SearchTerm = (data: TSearchTerm = {}) => {
  return StaffModel<TSearchTerm, SearchTermService>(data, 'search', SearchTermService);
};

export type TSearchTermInstance = ReturnType<typeof SearchTerm>;
