import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import { useRequest } from '@miq/hookjs';
import { getCN, Response, truncateStr, uuid } from '@miq/utiljs';
import { Icons, Img, Loading, SectionHeader } from '@miq/componentjs';

import { StaffSection } from '../components';

import { Hit, THitInstance } from './models';
import { THit } from './types';
import { hitIsBot, hitIsFb, hitIsIg, hitsByIp } from './utils';
import { HitDeleteButton, HitListDeleteButton } from './buttons';

export const HitSummarySection = (props: { title?: string }) => {
  const [params, setParams] = useSearchParams();
  const sDt = params.get('sdt') as string | Date;
  const { res, loading } = useRequest(() => Hit({}).getSummary(params), {
    refreshDeps: [sDt],
  });
  // const [dt, setDt] = React.useState<Date>(new Date().subDays(4));
  const { title = 'Summary' } = props;

  if (loading) return <Loading />;

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const items = r.data || [];
  const perDate: any = {};
  items.forEach((i) => {
    const key = i.created__date;
    perDate[key] = Object.keys(perDate).includes(key) ? [...perDate[key], i] : [i];
  });

  // console.log(perDate);

  return (
    <StaffSection
      border
      {...{ title }}
      requiredPerms={['analytics.view_hit']}
      className="bg-white summary-section"
      bodyCN="p-0"
    >
      <div style={{ height: 320, overflowY: 'auto' }}>
        {Object.entries(perDate)?.map((i) => {
          const [date, items] = i;
          return (
            <div key={uuid()} className="mb-3">
              <div className="text-center" style={{ position: 'sticky', top: 2 }}>
                <span className="bg-white p-1 rounded m-1">{new Date(date).tomorrow().toDateString()}</span>
              </div>
              {items.map((h) => {
                return (
                  <div key={uuid()} style={{ marginBottom: 6 }} className="border-bottom">
                    <div className="d-flex justify-content-between">
                      <span>{h.path}</span>
                      <span>{h.count}</span>
                    </div>
                  </div>
                );
              })}
            </div>
          );
        })}
      </div>
    </StaffSection>
  );
};

const HitListItem = ({ item }: { item: THit }) => {
  const [isDeleted, setDeleted] = React.useState(false);

  if (isDeleted) return null;

  const instance = Hit(item);
  const { url, referrer, response_status, created } = instance;

  return (
    <div className="hit-list-item d-grid grid-md-5 text-sm py-1">
      <div className="span-md-2">
        <a href={url} target="_blank" title={url}>
          {item.path}
        </a>

        <div className="text-muted" title="session key">
          {item.session}
        </div>

        <span className={getCN([response_status! > 299 ? 'text-danger' : 'text-success'])} title="response status">
          {response_status}
        </span>

        <span title="Date created">{` / ${new Date(created!).formatDateToStr()} | ${new Date(
          created!
        ).formatTime()}`}</span>
        {item.debug && <span>{` / Debug`}</span>}
      </div>

      <div className="" title={`Referrer: ${referrer}`}>
        {truncateStr(referrer || '')}
      </div>

      <div className="" title={`User agent: ${item.user_agent}`}>
        {truncateStr(item?.user_agent || '')}
      </div>

      <div className="">
        <HitDeleteButton instance={instance} onDelete={() => setDeleted(true)} />
      </div>
    </div>
  );
};

export const HitList = ({ items }: { items: THit[] }) => {
  const [view, setView] = React.useState<'ip' | 'path'>('path');

  switch (view) {
    case 'path':
      return (
        <div>
          <div onClick={() => setView('ip')} className="my-2">
            [ IP ]
          </div>
          {items.map((hit) => {
            const { session_data, ...i } = hit;
            const { query = {} } = session_data;
            return (
              <div className="mb-1 p-1 rounded border-1" key={uuid()}>
                <SectionHeader
                  header={
                    <div className="d-flex align-items-center">
                      <div style={{ width: '48px', minWidth: 48 }} className="me-1">
                        <HitImg hit={hit} />
                      </div>

                      <div className="text-sm">
                        <div>
                          <HitDate {...{ hit }} className="text-muted me-1" />
                          <HitUser {...{ hit }} className="me-1" />
                          <HitCart {...{ hit }} className="bg-green-100 me-1" />
                        </div>
                        <div title={i.url} className="">
                          {i.path}
                        </div>
                        <div className="">
                          {Object.entries(query).map(([key, val = []]) => {
                            return (
                              <span key={uuid()} className="text-sm text-muted me-1">
                                {key}: {val.join(' ')}
                              </span>
                            );
                          })}
                        </div>
                      </div>
                    </div>
                  }
                  text={
                    <>
                      {i.referrer && <div>{`  ${i.referrer}`}</div>}
                      {i.user_agent && <div>{i.user_agent}</div>}
                    </>
                  }
                />
              </div>
            );
          })}
        </div>
      );

    default:
      return (
        <div className="hit-list">
          <div onClick={() => setView('path')} className="my-2">
            [ PATH ]
          </div>

          {Object.entries(hitsByIp(items)).map((item) => {
            let [ip, h] = item;
            const hits = h.reverse();
            const isFb = Boolean(hits.filter((item: THit) => hitIsFb(item)).length);
            const isIg = hits.find((item: THit) => hitIsIg(item));
            const isBot = hits.find((item: THit) => hitIsBot(item));
            let user = hits[0]?.source_id?.match(/-(\w+)___/gm)?.[0]?.slice(1, -3);

            return (
              <StaffSection
                border
                title={
                  <div className="d-flex align-items-center">
                    <span className="d-flex">
                      {isBot && <Icons.Robot className="me-1 text-muted" width={20} height={20} />}
                      {isIg && <Icons.Instagram className="me-1" />}
                      {isFb && <Icons.Facebook className="text-primary me-1" />}
                    </span>
                    <span className="text-sm fw-bold">{user || ip}</span>
                  </div>
                }
                actions={
                  <div className="">
                    <HitListDeleteButton slugs={hits.map((i: THit) => i?.slug)} onDelete={() => {}} />
                  </div>
                }
                className="mb-1"
                bodyCN="p-0"
                key={ip}
              >
                {hits
                  .sort((a: THit, b: THit) => (new Date(a.created!) > new Date(b.created!) ? -1 : 1))
                  .map((i: THit) => (
                    <HitListItem item={i} key={i.slug} />
                  ))}
              </StaffSection>
            );
          })}
        </div>
      );
  }
};

type THitOrInstance = THit | THitInstance;

export const HitImg = ({ hit }: { hit: THitOrInstance }) => {
  const img = hit?.session_data?.img;
  return img ? <Img.Square src={`${img}`} className="rounded" /> : null;
};

export const HitDate = ({ hit, ...props }: { hit: THitOrInstance; className?: string }) =>
  hit?.created ? <span className={props?.className}>{new Date(hit?.created).formatDateToStr()}</span> : null;

export const HitUser = ({ hit, ...props }: { hit: THitOrInstance; className?: string }) => {
  const { ip, session_data = {}, customer_data = {} } = hit;
  const { username, _cus } = session_data || {};

  const name = `${customer_data?.first_name} ${customer_data?.last_name}`;

  return <span className={props?.className}>{name || _cus || username || ip}</span>;
};

export const HitCart = ({ hit, ...props }: { hit: THitOrInstance; className?: string }) => {
  const { _cart } = hit?.session_data || {};

  return _cart ? (
    <a href={`/staff/sales/cart/${_cart}/`} className={props?.className} target="_blank">
      view cart
    </a>
  ) : null;
};

export const HitQueries = ({ hit, ...props }: { hit: THitOrInstance; className?: string }) => {
  const { query = [] } = hit?.session_data;
  const excludeKeys = ['path', 'url', 'sdt', 'atc'];

  return (
    <>
      {Object.entries(query).map((entry) => {
        const [key, val = []] = entry;
        if (excludeKeys.includes(key)) return null;

        return (
          <span key={uuid()} className="text-sm text-muted me-1 bg-blue-50 rounded">
            {key}: {truncateStr(val.join(' '))}
          </span>
        );
      })}
    </>
  );
};
