import * as React from 'react';

import './view.scss';

import { getCN } from '@miq/utiljs';
import { withHasPerms } from './utils';
import { IViewProps, View } from '@miq/componentjs';

const SView = (props: IViewProps) => (
  <View
    {...props}
    className={getCN(['miq-staff-view', props.className])}
    mainCN={getCN(['miq-staff-view-main', props.mainCN])}
    footerCN={getCN(['miq-staff-view-footer', props.footerCN])}
  />
);

const StaffView = withHasPerms<IViewProps>(SView);

export default StaffView;
