import axios from 'axios';
import { DOMAIN } from './constants';

axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN';
axios.defaults.headers.post['Content-Type'] = 'application/json';

// axios.defaults.headers['Content-Type'] = 'text/html; charset=UTF-8';
// axios.defaults.headers.patch["Content-Type"] = "application/json";

export const HTTP = axios.create({
  baseURL: DOMAIN,
  timeout: 3000,
  withCredentials: true, // To enable cookies
  headers: { 'X-Requested-With': 'XMLHttpRequest', 'Content-type': 'application/json' },
  onUploadProgress: (progressEvt) => {
    // console.log(Math.round(progressEvt.loaded / progressEvt.total * 100) + '%')
  },
});

export class Response<T = any> {
  _res: any | null = null;

  constructor(res: any) {
    this._res = res;
  }

  first(): T {
    return this.items?.[0];
  }

  get items(): T[] {
    const data = this.data;
    if (!this.data) return [];
    return data.results;
  }

  get data() {
    if (!this.isSuccess) return null;
    return this._res.data;
  }

  get isSuccess(): boolean {
    const res = this._res;
    if (!res || !res.status || res.status >= 300) return false;
    return true;
  }

  get error() {
    if (this.isSuccess) return null;
    return this._res;
    // return RequestError(this._response, this._call.method, this._call.relativeUrl, this._call.body);
  }
}
