import { useSearchParams } from 'react-router-dom';

import { useRequest } from '@miq/hookjs';

import { Campaign, Hit, LIB } from './models';
import { TCampaign, TCampaignSummary, THit, TLIB } from './types';

export const useLIBListRequest = () => {
  const [params] = useSearchParams();

  return useRequest<TLIB>(
    () => LIB({}).list({ params })
    // {  refreshDeps: [summary],}
  );
};

export const useCampaignListRequest = () => {
  const [params] = useSearchParams();

  const summary = params.get('summary');

  return useRequest<TCampaign | TCampaignSummary>(() => Campaign({}).list({ params: { summary } }), {
    refreshDeps: [summary],
  });
};

export const useHitListRequest = (filterPath?: string, defParams?: any) => {
  const [params] = useSearchParams(defParams);

  const q = params.get('q');
  const path = params.get('path');
  const ref = params.get('ref');
  const ip = params.get('ip');
  const bot = params.get('bot');
  const ua = params.get('ua');
  const status = params.get('status');
  const dt = params.get('dt');

  let searchParams: any = { ...params, q };
  if (filterPath) searchParams['path'] = filterPath;

  return useRequest<THit>(() => Hit({}).list({ params: params }), {
    refreshDeps: [q, path, ref, ip, bot, ua, status, dt],
  });
};

export const hitsByPath = (hitList: THit[]) => {
  const data: Record<string, any> = {};

  hitList.forEach((hit) => {
    const { path } = hit;

    if (!Object.keys(data).includes(path)) data[path] = [];
    data[path] = [...data[path], hit];
  });

  return data;
};

export const hitsByIp = (hitList: THit[]) => {
  const data: Record<string, any> = {};

  hitList.forEach((hit) => {
    const { ip } = hit;
    if (!ip) return;

    if (!Object.keys(data).includes(ip)) data[ip] = [];
    data[ip] = [...data[ip], hit];
  });

  return data;
};

export const hitIsMobile = (data: THit) => {
  let { user_agent: ua } = data;
  if (!ua) return true;
  ua = ua.toLowerCase();
  return ua.includes('mobile') || ua.includes('iphone') || ua.includes('android');
};

export const hitIsBot = (data: THit) => {
  let { user_agent: ua, path } = data;
  if (!ua) return true;
  ua = ua.toLowerCase();
  return path.toLowerCase().includes('robot') || ua.includes('bot') || ua.includes('robot') || ua.includes('ahrefsbot');
};

export const hitIsIg = (data: THit) => {
  let { referrer: ref, user_agent: ua } = data;
  if (!ref || !ua) return false;

  ref = ref.toLowerCase();
  ua = ua.toLowerCase();
  return ref.includes('instagram') || ref.includes('ig') || ua.includes('instagram') || ua.includes('ig');
};

export const hitIsFb = (data: THit) => {
  let { referrer: ref = '', user_agent: ua = '' } = data;
  if (!ref || !ua) return false;

  ref = ref.toLowerCase();
  ua = ua.toLowerCase();

  return ref.includes('facebook') || ref.includes('fb') || ua.includes('facebook') || ua.includes('fb');
};
