export const IS_DEV: boolean = process.env.NODE_ENV !== 'production';

export const DOMAIN: string = IS_DEV
  ? process.env.REACT_APP_DOMAIN || `http://127.0.0.1:8000`
  : typeof window !== 'undefined'
  ? `${window.location.origin}`
  : '';

