import { Link, LinkProps, NavLink, NavLinkProps } from 'react-router-dom';

import { Button, ButtonProps, Icons, IViewSectionProps, ViewSection } from '@miq/componentjs';

import { withHasPerms } from './utils';
import { addForwardSlash, truncateStr } from '@miq/utiljs';

export const StaffNavLink = withHasPerms<NavLinkProps>(NavLink, { className: 'miq-staff-nav-link' });
export const StaffLink = withHasPerms<LinkProps>(Link, { className: 'miq-staff-link' });
export const StaffButton = withHasPerms<ButtonProps>(Button, { className: 'miq-staff-btn' });
export const StaffSection = withHasPerms<IViewSectionProps>(ViewSection);

type TSidebarLinkProps<T = any> = {
  href: string;
  label: string;
  icon?: ReturnType<typeof Icons.Gear>;
} & T;

export const SidebarLink = (props: TSidebarLinkProps) => {
  return (
    <a href={`/staff/${addForwardSlash(props.href)}`} className="miq-staff-nav-link">
      {props?.icon}
      <div className="label text-ellipsis">{truncateStr(props.label, 0, 10)}</div>
    </a>
  );
};
