import { Icons } from '@miq/componentjs';
import { getCN } from '@miq/utiljs';
import * as React from 'react';
import { Routes, Route, NavLink, NavLinkProps } from 'react-router-dom';

import StaffView from '../Views';

import * as Views from './views';

const HitListView = React.lazy(() => import('./views.HitList'));

export function AnalyticsStaffRoutes() {
  return (
    <StaffView title="Analytics" back="/staff/" mainCN="no-bg p-0" className="analytics-view">
      <div className="header-menu d-flex justify-content-center m-1">
        <TabLink to="/staff/analytics/" className="me-1 d-flex align-items-center">
          <Icons.House />
        </TabLink>

        <TabLink to="/staff/analytics/hits/" className="me-1">
          Hits
        </TabLink>

        <TabLink to="/staff/analytics/search/" className="me-1">
          Search
        </TabLink>

        <TabLink to="/staff/analytics/campaigns/" className="me-1">
          Campaigns
        </TabLink>

        <TabLink to="/staff/analytics/libs/" title="Links in bio">
          LIB
        </TabLink>
      </div>

      <Routes>
        <Route path="/search/*" element={<Views.SearchRoutes />} />
        <Route path="/campaigns/*" element={<Views.CampaignRoutes />} />
        <Route path="/libs/*" element={<Views.LIBRoutes />} />
        <Route path="/hits/*" element={<HitListView />} />
        <Route path="/" element={<Views.IndexView />} />
      </Routes>
    </StaffView>
  );
}

const TabLink = ({ className, ...props }: NavLinkProps) => (
  <NavLink
    className={({ isActive }: { isActive?: boolean }) =>
      getCN(['btn', `${className}`, isActive ? 'btn-primary' : 'btn-primary-3'])
    }
    {...props}
  />
);

export * from './utils';
export * from './types';
export * from './models';
