import { useEffect, useMemo, useReducer, useRef } from 'react';
import { AxiosPromise } from '@miq/utiljs';

// https://elijahmanor.com/blog/react-blessed-userequest

// https://hooks.umijs.org/hooks/async#refreshdeps
// https://github.com/alibaba/hooks/blob/master/packages/hooks/src/useRequest/src/useRequestImplement.ts

export type baseOptionsType = {
  refreshDeps?: any[];
  onSuccess?: (res?: any) => void;
  onError?: (errRes?: any) => void;
};

const baseOptions: baseOptionsType = {
  refreshDeps: [],
};

const defaultResponse = {
  loading: true,
  status: null,
  statusText: null,
  config: null,
  data: null,
  headers: null,
  request: null,
};

export function useRequest<T = any>(service: () => AxiosPromise<T> | null, options: baseOptionsType = baseOptions) {
  const [res, dispatch] = useReducer(responseReducer, defaultResponse);
  const isMountedRef = useRef(true);

  const { onSuccess, onError } = options;

  const makeRequest = useMemo(
    () => () => {
      const s = service?.();
      if (!s) {
        dispatch({ type: 'NOTLOADING' });
        return;
      }

      dispatch({ type: 'LOADING' });

      return s
        .then((payload: any) => {
          if (!isMountedRef.current) return null;

          dispatch({ type: 'SET_RES', payload });
          onSuccess?.(payload);
        })
        .catch((err: any) => {
          if (!isMountedRef.current) return null;
          dispatch({ type: 'SET_ERR', payload: err });
          onError?.(err);
          // throw err;
        });
    },
    [service, onSuccess, onError]
  );

  const { refreshDeps } = options;

  const deps = useMemo(() => {
    if (Array.isArray(refreshDeps)) {
      return [...refreshDeps, makeRequest];
    }
    return [makeRequest];
  }, refreshDeps);

  // console.log(refreshDeps, deps);

  useEffect(() => {
    makeRequest();
  }, deps);

  useEffect(() => {
    return () => {
      isMountedRef.current = false;
    };
  }, [isMountedRef]);

  return {
    res: res,
    loading: res.loading,

    addItem: (payload: any) => dispatch({ type: 'ADD_ITEM', payload }),
    updateItem: (payload: any) => dispatch({ type: 'UPDATE_ITEM', payload }),
    removeItem: (payload: any) => dispatch({ type: 'REMOVE_ITEM', payload }),

    updateData: (payload: any) => dispatch({ type: 'UPDATE_DATA', payload }),
    setData: (payload: any) => dispatch({ type: 'SET_DATA', payload }),
    removeData: () => dispatch({ type: 'REMOVE_DATA' }),

    setLoading: () => dispatch({ type: 'LOADING' }),
    setRes: (payload: any) => dispatch({ type: 'SET_RES', payload }),
    setError: (payload: any) => dispatch({ type: 'SET_ERR', payload }),

    refresh: makeRequest,
  };
}

const hasKey = (obj: any, key: string) => Object.keys(obj).includes(`${key}`);

type TAction =
  | {
      type: 'ADD_ITEM' | 'UPDATE_ITEM' | 'REMOVE_ITEM' | 'SET_DATA' | 'UPDATE_DATA' | 'SET_RES' | 'SET_ERR';
      payload: Record<string, any> & { slug: string };
    }
  | { type: 'LOADING' | 'REMOVE_DATA' };

const sendEvent = <Type extends TAction['type']>(
  ...args: Extract<TAction, { type: Type }> extends { payload: infer TPayload } ? [Type, TPayload] : [Type]
) => {};

const responseReducer = (state: typeof defaultResponse, action: any): typeof defaultResponse => {
  const { type, payload } = action;
  let data, results;

  switch (type) {
    case 'ADD_ITEM':
      data = state.data;
      if (hasKey(data, 'results')) {
        const { append, ...rest } = payload;
        if (append) results = [...data?.results, rest];
        else results = [rest, ...data?.results];

        data = { ...state.data, results, count: state.data.count == null ? 1 : state.data.count + 1 };
      }
      return { ...state, data };
    case 'UPDATE_ITEM':
      data = state.data;
      if (hasKey(data, 'results')) {
        results = data.results.map((i) => {
          if (i.slug === payload?.slug) return payload;
          return i;
        });
        data = { ...state.data, results };
      }
      return { ...state, data };
    case 'REMOVE_ITEM':
      data = state.data;
      if (hasKey(data, 'results')) {
        results = data.results.filter((i) => i.slug !== payload.slug);
        data = { ...state.data, results };
      }
      return { ...state, data };

    case 'UPDATE_DATA':
      return { ...state, data: { ...state.data, ...payload } };
    case 'SET_DATA':
      return { ...state, data: payload };
    case 'REMOVE_DATA':
      return { ...state, data: null };

    case 'LOADING':
      return { ...state, loading: true };
    case 'NOTLOADING':
      return { ...state, loading: false };

    case 'SET_RES':
    case 'SET_ERR':
      return { ...state, ...payload, loading: false };

    default:
      return { ...state };
  }
};
