import { getCN } from '@miq/utiljs';
import React from 'react';
import { Link } from 'react-router-dom';

import './views.scss';

interface IViewHeaderProps extends Omit<React.ComponentPropsWithoutRef<'div'>, 'title'> {
  back?: string;
  title?: string | React.ReactNode;
  text?: string | React.ReactNode;
  actions?: React.ReactNode;
  header?: React.ReactNode;
  headerCN?: string;
}

export interface IViewProps extends IViewHeaderProps {
  loading?: boolean | string;
  footer?: React.ReactNode;
  mainCN?: string;
  mainStyle?: Extract<React.ComponentProps<'div'>, 'style'>;
  footerCN?: string;
}

export type TView = typeof View & { TabSection: React.FC };

export const View = ({ children, ...props }: IViewProps) => {
  const { header, title, text, actions, back, footer, loading, mainCN, mainStyle, footerCN, headerCN, ...rest } = props;
  return (
    <div {...rest} className={getCN(['miq-view', props.className])}>
      <ViewHeader {...{ header, title, text, actions, back, headerCN }} />

      <div className={getCN(['miq-view-main', mainCN])} style={mainStyle}>
        {children}
      </div>

      {footer && <footer className={getCN(['miq-view-footer', footerCN])}>{footer}</footer>}
    </div>
  );
};

const ViewHeader = ({ header, title, text, actions, back, headerCN }: IViewHeaderProps) => {
  const hasHeader = header || title || text || actions || back;

  if (!hasHeader) return null;

  if (header) return <div className={getCN(['miq-view-header-wrapper', headerCN])}>{header}</div>;

  return (
    <header className={getCN(['miq-view-header-wrapper', headerCN])}>
      <div className="miq-view-header d-flex">
        <div className="d-flex align-items-center">
          {back && (
            <Link
              to={back}
              className="btn miq-view-header-back me-1 d-flex align-items-center justify-content-center"
              title="Go back"
              style={{ width: 30, height: 30, borderRadius: '50%' }}
            >
              {`<`}
            </Link>
          )}

          <div className="miq-view-header-title">
            {typeof title === 'string' ? <div className="miq-view-header-title-text">{title}</div> : title}
          </div>
        </div>

        {actions && <section className="miq-view-header-actions">{actions}</section>}
      </div>

      {text && <div className="miq-view-header-text">{text}</div>}
    </header>
  );
};

//#region  ======================================= VIEWSECTION

interface ISectionHeaderProps extends Omit<IViewHeaderProps, 'back'> {
  headerClsPrefix?: string;
}

export interface IViewSectionProps extends Omit<IViewProps, 'back'>, ISectionHeaderProps {
  border?: string | boolean;
  bodyCN?: string;
}

export function ViewSection({ children, ...props }: IViewSectionProps) {
  const { className, bodyCN, footer, border, style, ...rest } = props;

  return (
    <div className={getCN(['miq-view-section', border && 'border', className, !!children && 'mb-1'])} style={style}>
      <SectionHeader {...rest} />
      {children && <div className={`${getCN(['miq-view-section-body', bodyCN])}`}>{children}</div>}
      {footer && <div className="miq-view-section-footer">{footer}</div>}
    </div>
  );
}

export const SectionHeader = (props: ISectionHeaderProps) => {
  const { header, title, text, actions, headerClsPrefix = 'miq-view-section' } = props;
  const hasHeader = header || title || text || actions;

  if (!hasHeader) return null;

  return (
    <div className={getCN([`${headerClsPrefix}-header`, props.headerCN])}>
      {header ? (
        header
      ) : (
        <div className="d-flex align-items-center justify-content-between">
          <div className={`${headerClsPrefix}-header-title`}>{title}</div>

          {actions && <section className={`${headerClsPrefix}-header-actions`}>{actions}</section>}
        </div>
      )}

      {text && <div className={`${headerClsPrefix}-header-text`}>{text}</div>}
    </div>
  );
};
//#endregion
