export const getFileIcon = (ext: string) => {
  switch (ext) {
    case '.pdf':
      return FilePdf;

    case '.docx':
      return FileWord;

    case '.zip':
      return FileZip;

    case '.png':
    case '.jpg':
    case '.psd':
    case '.jpeg':
      return FileImage;

    default:
      return File;
  }
};

