import { useSearchParams } from 'react-router-dom';

import { Table } from '@miq/componentjs';
import { Response, uuid } from '@miq/utiljs';

import StaffView from '../Views';

import { TCampaign, TCampaignSummary } from './types';
import { useCampaignListRequest } from './utils';
import Staff from '..';

export default function CampaignIndexView() {
  const [params] = useSearchParams();
  const summary = params.get('summary');

  const { res } = useCampaignListRequest();
  const r = new Response<TCampaignSummary | TCampaign>(res);
  if (!r.isSuccess) return null;

  return (
    <StaffView
      title="Campaigns"
      text={`${r.data.count} items`}
      actions={<Staff.Link to="./?summary=1">Summary</Staff.Link>}
    >
      <Table
        className="w-100"
        items={r.items}
        header={
          <>
            <Table.Th />
            <Table.Th>Value</Table.Th>
            <Table.Th>Count</Table.Th>
            <Table.Th />
          </>
        }
        renderItem={(i) => {
          return (
            <Table.Tr key={uuid()}>
              <Table.Td>
                <div>{i.key}</div>
              </Table.Td>
              <Table.Td>{i.value}</Table.Td>
              <Table.Td>{i?.count}</Table.Td>
              <Table.Td>{/* Actions */}</Table.Td>
            </Table.Tr>
          );
        }}
      />
    </StaffView>
  );
}
