import * as React from 'react';
import { Route, Routes } from 'react-router-dom';

import { Response } from '@miq/utiljs';

import StaffView from '../Views';
import { StaffSection } from '../components';

import { useHitListRequest } from './utils';
import CampaignIndexView from './views.CampaignIndex';
import { HitList, HitSummarySection } from './components';

const LIBListView = React.lazy(() => import('./views.LIBList'));
const LIBUpdateView = React.lazy(() => import('./views.LIBUpdate'));

export const LIBRoutes = () => {
  return (
    <Routes>
      <Route path="/:libSlug/" element={<LIBUpdateView />} />
      <Route index element={<LIBListView />} />
    </Routes>
  );
};

export const SearchRoutes = () => {
  return (
    <Routes>
      <Route path="/" element={<StaffView title="Search"></StaffView>} />
    </Routes>
  );
};

export const CampaignRoutes = () => {
  return (
    <Routes>
      <Route path="/" element={<CampaignIndexView />} />
    </Routes>
  );
};

export const IndexView = () => {
  const { res } = useHitListRequest();

  const r = new Response(res);

  if (!r.isSuccess) return null;

  return (
    <StaffView mainCN="no-bg p-0">
      <div className="mt-3">
        <HitSummarySection />
      </div>

      <StaffSection title="Last 100 hits">
        <HitList items={r.items} />
      </StaffSection>
    </StaffView>
  );
};
