import * as React from 'react';

import { getCN } from '@miq/utiljs';
import { Button, ConfirmButton, Icons } from '@miq/componentjs';

import { Hit, THitInstance } from './models';

export const HitDeleteButton = ({
  instance,
  ...props
}: {
  label?: string;
  instance: THitInstance;
  onDelete?: (payload?: any) => void;
}) => {
  const { label = '', onDelete } = props;

  const handleDelete = () => {
    instance
      .destroy()
      .then((res) => {
        onDelete?.(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <ConfirmButton
      renderHeader={() => (
        <div className="d-flex align-items-center">
          <Icons.ExclamationTriangleFill />
          <span className="ms-1">Please confirm</span>
        </div>
      )}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={handleDelete} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          <div>You're about to delete this hit: {`${instance.slug}`}</div>
          <div>You cannot undo this action</div>
        </div>
      )}
      className={getCN(['btn-danger-3'])}
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};

export const HitListDeleteButton = ({
  slugs,
  ...props
}: {
  label?: string;
  slugs: string[];
  onDelete?: (payload?: any) => void;
}) => {
  const { label = '', onDelete } = props;

  const handleDelete = (setOpen: any) => {
    Hit({})
      .deleteSlugs(slugs)
      .then((res) => {
        onDelete?.(res.data);
        setOpen(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <ConfirmButton
      renderHeader={() => (
        <div className="d-flex align-items-center">
          <Icons.ExclamationTriangleFill />
          <span className="ms-1">Please confirm</span>
        </div>
      )}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={() => handleDelete(setOpen)} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          <div>You're about to delete {`${slugs.length} hits.`}</div>
          <div>You cannot undo this action</div>
        </div>
      )}
      className={getCN(['btn-danger-3'])}
      title="Delete group"
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};
